body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  font-family: 'Futura', Fallback, sans-serif;
}

#cursor{
  backdrop-filter: blur(5px) saturate(180%);
  -webkit-backdrop-filter: blur(5px) saturate(180%);
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  border-radius: 5px;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  justify-content: center;
}

.cursor-overlay{
  width: 0;
  height: 100%;
  transition: all 0.5s ease-in-out;
  position: absolute;
  left: 0;
  border-radius: 10px;
  background-color: rgb(0, 0, 0);
}

.cursor-inner{
  color: white;
  font-weight: 900;
  text-align: center;
}

.line{
  color: #545D6E;
  line-height: 2;
  font-size: 0.9vw;
  width: 0;
  overflow: hidden;
  white-space: nowrap;
  font-family:  "'Poppins', sans-serif";
  font-weight: 600;
  left: 5%;
  bottom: 26%;
}

.futura{
  font-family: 'Futura', Fallback, sans-serif;
}

.char{
  font-size: 10px;
}

.hide{
  width: 0;
  max-width: 0;
  overflow: hidden;
  white-space: nowrap;
}

.active .heading{
  margin-top: 0px;
  font-weight: 700;
  color: #715ffe;
}

.active .underline{
  height: 2px;
  margin: auto;
  background-color: #715ffe;
  width: 80%;
}

.nav{
  transform: translateY(-100px);
}

.link{
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.link:hover{
  color: #715ffe;
  text-decoration: underline;
}

.chip{
  border-radius: 20px;
  padding: 4px 18px 4px 18px;
  border: 1px solid #0a0d0d;
  box-shadow: inset 0px 0px 20px rgb(0 0 0 / 50%);
}

.project{
  width: 18vw;
  position: absolute;
  top: 10px;
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
}

.project .img{
  transition: all 0.3s ease-in-out;
  width: 0;
}

.project .img2{
  transition: all 0.3s ease-in-out;
  height: 0;
  max-height: 0;
}

.project:hover .img{
  width: 100%;
}

.project:hover .img2{
  height: 100%;
  max-height: 1000px;
}

.button {
  display: inline-block;
  height: -webkit-min-content;
  height: min-content;
  margin-top: -200px;
  margin-bottom: 40px;
  position: relative;
  margin-right: 30px;
  transform: translateY(100px);
}

.button.is-play {
  background-color: #fff;
  border-radius: 50%;
  width: 60px;
  height: 60px;
}

.button.is-play .button-outer-circle {
  background: rgba(255,255,255,0.2);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}

.button.is-play .button-icon {
  position: absolute;
  top: 25%;
  left: 28%;
  width: 30px;
  height: 30px;
}

.button.is-play .button-icon .triangle {
  animation: fadeIn 7s alternate infinite ease;
}

.button.is-play .button-icon .path {
  stroke-dasharray: 90;
  stroke-dashoffset: 0;
  animation: triangleStroke 3s alternate infinite;
  animation-timing-function: ease;
}

.has-scale-animation {
  animation: smallScale 3s infinite;
}

.has-delay-short {
  animation-delay: 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes triangleStroke {
  from {
    stroke-dashoffset: 90;
  }

  to {
    stroke-dashoffset: 0;
  }
}

@keyframes smallScale {
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(1.5);
    opacity: 0;
  }
}

.loading {
  background: rgb(204, 204, 204);
  background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
  border-radius: 5px;
  background-size: 200% 100%;
  animation: 1.5s shine linear infinite;
}

@keyframes shine {
  to {
    background-position-x: -200%;
  }
}

